import { Component, OnInit,Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA, MatDialog } from '@angular/material';
import { IWindow } from '../../classes/window';
@Component({
  selector: 'app-voice-search-model',
  templateUrl: './voice-search-model.component.html',
  styleUrls: ['./voice-search-model.component.css']
})
export class VoiceSearchModelComponent implements OnInit {
  public showDefualtSpeak:boolean = true;
  public searchText:string = "";
  public recognition:any;
  constructor(public dialogRef: MatDialogRef<VoiceSearchModelComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any) {
      dialogRef.disableClose = true;
     }

  ngOnInit() {
    const {webkitSpeechRecognition}:IWindow = <IWindow>window; 
    this.recognition = new webkitSpeechRecognition();
    this.recognition.lang = 'US-en';
    this.recognition.interimResults = true;
    this.recognition.start();
    this.recognition.addEventListener('result',(e) =>{
      this.showDefualtSpeak = false;
      this.searchText = e.results[0][0].transcript;
    })
   
   this.recognition.addEventListener('speechend', () => { 
     setTimeout(() => {
      this.stopRecord();
     }, 1000);
   });
  }


  stopRecord(){
    this.recognition.stop();
    this.dialogRef.close(this.searchText);
  }

  closeModel(){
   this.dialogRef.close();
  }
  
}
